	module.exports = {"data":{"markdownRemark":{"fileAbsolutePath":"/home/luanorlandi/git/angeloocana/src/pages/blog/git/10-tips-to-start-using-github-and-cmd.pt.md","html":"<p>Não perca mais seus arquivos! Mantenha um historico com todas as alterações, salve nas nuvems e fique tranquilo.</p>\n<p>Aqui você vai aprender dicas simples pra se tornar um <strong>NINJA</strong> usando <strong>Github</strong> e <strong>Linha de commando</strong>.</p>\n<h3>1. Acesse <a href=\"https://github.com\">github.com</a>.</h3>\n<p>  Click <strong>Sign In</strong> (Login) ou <strong>Sign Up</strong> (Cadastre-se).</p>\n<h3>2. Click no botão <strong>Start a project</strong> na página inicial.</h3>\n<ul>\n<li>Escolha o nome do Repositório, a URL do seu projecto vai ser\ngithub.com/USER-NAME/REPOSITORY-NAME</li>\n<li>Click no botão <strong>Create repository</strong></li>\n</ul>\n<p>  Parabéns! O seu repositório esta pronto pra receber os arquivos que você nunca mais vai perder.</p>\n<h3>3. Instalar Git (Se você ja não tem instalado).</h3>\n<ul>\n<li>Arch linux: <code>pacman -S git</code></li>\n<li>Windows: Baixe e Instale <a href=\"https://git-for-windows.github.io/\">https://git-for-windows.github.io/</a></li>\n<li>Mac: Va para a App Store e Instale o <strong>XCode</strong></li>\n</ul>\n<h3>4. Baixe o repositório para a sua máquina</h3>\n<p>Abra o terminal ou qualquer programa no seu computador que de acesso a linha de comando.</p>\n<p>Digite os comandos abaixo para navegar entre as pastas do sistema ate encontrar a pasta desejada.</p>\n<ul>\n<li><code>ls</code> ou <code>dir</code> listam os arquivos e pastas do diretório atual.</li>\n<li><code>cd FOLDER_NAME</code> abre a pasta.</li>\n<li><code>cd ..</code> sobe um nível 1 pasta.</li>\n</ul>\n<p>Clone o repositório para o seu PC:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">git</span> clone https://github.com/YOUR_USER_NAME/REPOSITORY_NAME\n</code></pre>\n      </div>\n<p>Entre na pasta:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">cd</span> REPOSITORY_NAME\n</code></pre>\n      </div>\n<p>uhuhuh Agora salve e adicione os seus arquivos para esta nova pasta.</p>\n<h3>5. Veja as alterações:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">git</span> status\n</code></pre>\n      </div>\n<h3>6. Adicione as alterações para um commit:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">git</span> add <span class=\"token keyword\">.</span>\n</code></pre>\n      </div>\n<p>  Não esqueça o ponto final <strong>.</strong></p>\n<h3>7. Crie o commit</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">git</span> commit -m <span class=\"token string\">'What did I... '</span>\n</code></pre>\n      </div>\n<p>  Escreve uma mensagem curta explicando as alterações que você fez, tente manter um bom histórico.</p>\n<h3>8. Publicar! Salve seus arquivos e alterações na nuvem</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">git</span> push\n</code></pre>\n      </div>\n<h3>9. Atualize sua versão local:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>    <span class=\"token function\">git</span> pull\n</code></pre>\n      </div>\n<h3>10. Va dormir melhor sabendo que seus arquivos estão salvos.</h3>\n<p>Quer aprender coisa mais avançada? <a href=\"https://angeloocana.com/pt/blog/git/git-setup/\">Click aqui e se torne um ninja</a>.</p>","excerpt":"Não perca mais seus arquivos! Mantenha um historico com todas as alterações, salve nas nuvems e fique tranquilo. Aqui você vai aprender…","fields":{"tagSlugs":[{"tag":"Git","link":"/pt/tags/git/"},{"tag":"versionamento de arquivos","link":"/pt/tags/versionamento-de-arquivos/"}],"slug":"/pt/blog/git/10-tips-to-start-using-github-and-cmd/","readNextPosts":[{"excerpt":"Yaourt é o gerenciador de pacotes da comunidade para o Arch Linux. Quando você usa o  pacman  (gerenciador de pacotes oficial) você precisa…","frontmatter":{"title":"Como instalar Yaourt no Arch Linux","date":"2017-08-30"},"fields":{"slug":"/pt/blog/linux/arch/install-yaourt/","langKey":"pt"}},{"excerpt":"Habilite auto complete, Commit com titulo e descrição, navege pelo histórico, atualize fork e muito mais! Habilitar auto complete ao digitar…","frontmatter":{"title":"Git comandos Avançados","date":"2017-06-23"},"fields":{"slug":"/pt/blog/git/git-setup/","langKey":"pt"}},{"excerpt":"O seu telefone esta lotado? Faça backup dos seus arquivos pro Arch Linux.\nVai ser fácil seguindo este tutorial (Eu espero hahaha).\nEu tive…","frontmatter":{"title":"Como salvar fotos do celular pro Arch Linux","date":"2017-08-30"},"fields":{"slug":"/pt/blog/linux/arch/backup-android-files/","langKey":"pt"}}]},"frontmatter":{"youtubeId":null,"title":"10 dicas para começar a usar Github e Linha de comando","tags":["Git","versionamento de arquivos"],"date":"2017-09-21","structuredData":{"type":"TechArticle","author":"angeloocana","dependencies":"Um computador","proficiencyLevel":"Beginner","articleSection":"Git","pageEnd":"...","pageStart":"...","pagination":"...","about":{"name":"Git","alternateName":"Github","description":"Git is a free and open source distributed version control system designed to handle everything from small to very large projects with speed and efficiency.","identifier":"git","image":"...","sameAs":"https://git-scm.com/"},"accessMode":"textual","accessModeSufficient":"textual","accessibilityAPI":"ARIA","accessibilityControl":"fullKeyboardControl, fullTouchControl, fullMouseControl","accessibilitySummary":"...","aggregateRating":"...","audience":"...","alternativeHeadline":"...","comment":"...","commentCount":"...","contentLocation":"...","dateCreated":"...","dateModified":"...","datePublished":"...","discussionUrl":"...","educationalUse":"...","isAccessibleForFree":true,"isFamilyFriendly":true,"keywords":"...","locationCreated":"Ottawa CA","thumbnailUrl":"...","version":1,"video":"..."}}}},"pathContext":{"path":"/pt/blog/git/10-tips-to-start-using-github-and-cmd/","langKey":"pt"}}

